body{
  width: 900vw;
  height: 100vh;
  overflow-y: hidden;
  overflow-x: scroll;
  margin: 0;
}

.App {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  box-sizing: content-box;
  display: flex;
  flex-direction: row;
  width: 1170vw;
}

.event {
  width: 200vw;
  height: 100vh;
}

.Slide {
  background-image: url('./images/911634.jpg');
  background-size: fit;
  background-position: 0% 40%;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.Slide h1{
  font-size: 10em;
  color: rgb(7, 7, 7);
}

.Slide h3{
  font-size: 5em;
}

.event {
  padding: 0 6em;
  border-right: black;
}

.event-0{
  background: linear-gradient(
    rgba(0, 0, 0, 0.4), 
    rgba(0, 0, 0, 0.4)
    ),url('./images/faw1.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
}

.event-0 h1{
  font-size: 3vw;
}

.event-0 p{
  font-size: 1.2vw;
  padding: 1em 4em;

}

.event-1{
  background-image: url('./images/lead_hoyos_bildarchivaustria.at_.jpg');
  color: white;
}

.event-1 p{
  font-size: 1.3vw;
  text-align: left;
  padding: 0 3em;
}

.event-1 h1{
  font-size: 3.6vw;
}

.event-2 {
  background: linear-gradient(
    rgba(0, 0, 0, 0.4), 
    rgba(0, 0, 0, 0.4)
    ),url('./images/Ultimatum\ v2.png');
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
}

.event-2 h1{
  margin-top: 1.4em;
}

.event-2 p {
  padding: 1em 4em;
  font-size: 1.1vw;
}

.event-3 {
  background-image: url('./images/Ed32-AustrianMilitary.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

.event-3 p {
  color: white;
  padding: 4em 6em;
  font-size: 1.2vw;
}

.event-4 {
  background-image: url('./images/2443.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0% 35%;
}

.event-4 p {
  color: blue;
  padding: 4em 6em;
  font-size: 1.2vw;
}

.event-4 .p-span {
  background-color: rgba(255, 255, 255, 0.226);
}

.event-5 {
  background-image: url('./images/evening_world_aug_1_1914.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0% 35%;  
}

.event-5 span{
  color: white;
  background-color: rgba(0, 0, 54, 0.425);
  padding: 0;
}

.event-5 p{
  padding: 3em 6em;
  font-size: 1.5vw;
}

.event-6 {
  background-image: url('./images/battleofbelgium.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0% 35%;
}

.event-6 p{
  padding: 2em 4em;
  font-size: 1.2vw;
  color: white;
}

.event-6 .p-span {
  background-color: rgba(0, 0, 0, 0.123);
}

.event-7{
  background: linear-gradient(
    rgba(0, 0, 0, 0.4), 
    rgba(0, 0, 0, 0.4)
    ),url('./images/greatbritainwar.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0% 35%;
  color: white;
}

.event-7 p {
  padding: 1em 3em;
  font-size: 1.2vw;
}

.trench h1 {
  margin: 0;
  padding: 0;
}

.weapon-stuff{
  display: flex;
}

.weapons img {
  width: 15vw;
}

.weapon {
  padding: 2em 7em;
}


